import React, { Component } from 'react';
import Button from 'react-bootstrap/Button';
import {
  PieChart,
  Pie,
  Tooltip,
  ResponsiveContainer,
  Cell,
  Legend,
  Line
} from 'recharts';

class Event extends Component {
  state = {
    events: [],
    showDetails: false
  };

  handleShowDetails = () => {
    if (this.state.showDetails === false) {
      this.setState({ showDetails: true });
    } else {
      this.setState({ showDetails: false });
    }
  };
  getRSVPData = () => {
    const rsvpCount = this.props.event.yes_rsvp_count;
    const rsvpLimit = this.props.event.rsvp_limit - rsvpCount;

    return [
      { name: 'Reservations', value: rsvpCount },
      { name: 'Open Spots', value: rsvpLimit }
    ];
  };

  render() {
    const { event } = this.props;
    let colors = ['#e63070', '#ff8a00'];
    return (
      <div className="Event">
        <div className="detailsOverview">
          <span className="eventName">Event: {event.name}</span> <br></br>
          <span>Date: {event.local_date}</span>
          <span> Time: {event.local_time}</span>
          <Button
            className="btn details-btn"
            onClick={() => this.handleShowDetails()}>
            Show
          </Button>
        </div>

        {this.state.showDetails && (
          <div className="expandedDetails">
            <p
              className="description"
              dangerouslySetInnerHTML={{ __html: event.description }}
            />
            {this.props.event.rsvp_limit ? (
              <ResponsiveContainer height={200}>
                <PieChart
                  width={20}
                  height={20}
                  margin={{ top: 0, right: 0, left: 30, bottom: 0 }}>
                  <Pie
                    isAnimationActive={false}
                    data={this.getRSVPData()}
                    cx={50}
                    cy={50}
                    outerRadius={80}
                    fill="#82ca9d"
                    dataKey="value"
                    nameKey="name">
                    {this.getRSVPData().map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={colors[index]} />
                    ))}
                  </Pie>
                  <Tooltip />
                  <Legend verticalAlign="top" height={36} />
                  <Line
                    name="Attending"
                    type="monotone"
                    dataKey="rsvpCount"
                    stroke="green"
                  />
                  <Line
                    name="Available Spots"
                    type="monotone"
                    dataKey="rsvpLimit"
                    stroke="yellow"
                  />
                </PieChart>
              </ResponsiveContainer>
            ) : (
              ''
            )}
            <Button
              className="btn details-btn"
              onClick={() => this.handleShowDetails()}>
              Hide
            </Button>
          </div>
        )}
      </div>
    );
  }
}

export default Event;
